@import "https://fonts.googleapis.com/css2?family=DM+Sans:opsz,wght@9..40,400;9..40,500;9..40,600;9..40,700;9..40,800;9..40,900;9..40,1000&family=Titan+One&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --Black: #000;
  --White: #fff;
  --Pink: #ffa5ba;
  --Light-Pink: #ffb8ca;
  --Pinkish-White: #fff5f6;
  --Dark-Red-Pink: #d41443;
  --Light-Brown: #f0d1a5;
  --Light-Green: #c2e297;
  --Brown: #907e82;
  --Brown-Transparent: rgba(212, 20, 67, .1);
  --Grey: #e1e1e1;
}

* {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  color: var(--White);
  letter-spacing: .56px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 28.16px;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 60px 0;
}

.link {
  color: inherit;
  text-decoration: none;
}

.list {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  display: inline-block;
}

.button {
  text-align: center;
  background-color: var(--Dark-Red-Pink);
  color: inherit;
  border-radius: 20px;
  margin-right: 12px;
  padding: 6px 16px;
  display: inline-block;
}

.button:hover {
  color: var(--Dark-Red-Pink);
  background-color: var(--White);
}

.image {
  width: 100%;
  height: auto;
  display: block;
}

.heading-two {
  color: var(--Dark-Red-Pink);
  letter-spacing: 3.48px;
  text-transform: uppercase;
  font-family: Titan One;
  font-size: 58px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.heading-three {
  letter-spacing: 1.2px;
  text-transform: uppercase;
  font-family: Titan One;
  font-size: 38px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.heading-four {
  letter-spacing: 1.2px;
  text-transform: uppercase;
  font-family: Titan One;
  font-size: 30px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.icon {
  width: 20px;
  height: 20px;
  fill: currentColor;
}

.icon-xl {
  width: 54px;
  height: 40px;
  fill: #e1e1e1;
}

.icon-l {
  width: 50px;
  height: 50px;
  fill: var(--Pink);
}

.icon-m {
  width: 34px;
  height: 34px;
  fill: currentColor;
}

.icon-s {
  width: 12px;
  height: 12px;
  fill: var(--Dark-Red-Pink);
}

.icon-xs {
  width: 4px;
  height: 8px;
  fill: currentColor;
}

.nav {
  background-color: var(--Light-Pink);
  z-index: 3;
  font-weight: 700;
}

.nav .container {
  padding-bottom: 0;
}

.nav-bar {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav-logo {
  width: 200px;
  height: 50px;
  background: url("ice-cream-logo.460987dd.svg") center no-repeat;
}

.nav-list {
  z-index: 3;
  gap: 35px;
  display: flex;
  position: relative;
  right: 15px;
}

.active {
  color: var(--Dark-Red-Pink);
}

.nav-button {
  align-items: center;
  gap: 32px;
  display: flex;
}

.nav-button .menu {
  z-index: 3;
  color: var(--Dark-Red-Pink);
  display: none;
  position: relative;
}

.nav-button .button, .nav-menu-wrapper .button {
  color: var(--Dark-Red-Pink);
  background-color: var(--White);
  align-items: center;
  display: flex;
}

.nav-menu-wrapper, .is-hidden {
  display: none;
}

@media (max-width: 779px) {
  .container {
    width: 320px;
  }

  .nav-bar {
    padding: 0 20px;
  }

  .nav-list, .nav-button .button {
    display: none;
  }

  .nav-button .menu {
    display: block;
  }

  .nav-menu {
    width: 100%;
    height: 100%;
    z-index: 999999999;
    background-color: #e17992;
    position: fixed;
    top: 0;
    left: 0;
  }

  .nav-menu-container {
    width: 320px;
    height: 100vh;
    margin: 0 auto;
    padding-top: 70px;
  }

  .nav-menu-wrapper {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 19px;
    margin-top: 25px;
    display: flex;
  }

  .nav-menu-list {
    width: 100%;
  }

  .nav-menu .close {
    position: relative;
    left: 88%;
  }

  .nav-menu-item {
    border-top: 1px solid #e18298;
    padding: 10px;
  }

  ul:last-child {
    border-bottom: 1px solid #e18298;
  }

  .nav-menu-wrapper .button {
    width: 210px;
    justify-content: center;
    gap: 10px;
  }
}

@media (min-width: 780px) and (max-width: 1199px) {
  .nav-bar {
    padding: 0 32px;
  }

  .nav-list {
    display: none;
  }

  .nav-button .menu {
    display: block;
  }

  .nav-menu {
    width: 100%;
    height: 100%;
    z-index: 999999999;
    background-color: #e17992;
    position: fixed;
    top: 0;
    left: 0;
  }

  .nav-menu-container {
    width: 780px;
    height: 100vh;
    margin: 0 auto;
    padding: 60px 32px;
  }

  .nav-menu-wrapper {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 15px;
    display: flex;
  }

  .nav-menu-list {
    width: 100%;
    flex-direction: column;
    display: flex;
  }

  .nav-menu-item {
    border-top: 1px solid #e18298;
    padding: 10px;
  }

  ul:last-child {
    border-bottom: 1px solid #e18298;
  }

  .nav-menu-wrapper .button {
    width: 210px;
    justify-content: center;
  }

  .nav-menu .close {
    position: relative;
    left: 88%;
  }
}

.hero {
  background-color: var(--Light-Pink);
}

.hero .container {
  padding: 0;
}

.hero .heading-two {
  letter-spacing: 2.32px;
  line-height: 90.5%;
}

.hero .heading-three {
  letter-spacing: 1.52px;
}

.hero-wrapper {
  justify-content: space-between;
  padding-top: 60px;
  display: flex;
}

.hero-headings {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.hero-business-wrapper > div {
  flex-direction: column;
  justify-content: center;
  align-items: end;
  margin: 18px 20px 0 0;
  display: flex;
}

.hero-business {
  position: relative;
}

.hero-business .heading-two {
  z-index: 1;
  font-size: 46px;
}

.hero-business:before {
  content: "";
  width: 30px;
  height: 30px;
  background-color: var(--White);
  border-radius: 50%;
  display: block;
  position: absolute;
  top: -10px;
  right: -15px;
}

.hero-slider {
  padding-bottom: 60px;
  position: relative;
}

.slider.one {
  width: 215px;
  height: 215px;
  background: url("hero-1.c9e3aae3.png") center no-repeat;
  margin-top: 40px;
  margin-left: 50px;
}

.slider.two {
  position: absolute;
  top: -124%;
  right: 12%;
}

.slider-bg {
  width: 535px;
  height: 535px;
  background-color: var(--Pink);
  border-radius: 50%;
  display: block;
}

.slider.two:after {
  content: "";
  width: 235px;
  height: 555px;
  background: url("hero-2.b6bc4fdc.png") center no-repeat;
  display: block;
  position: absolute;
  top: 11.6%;
  right: 34%;
}

.slider.three {
  width: 305px;
  height: 240px;
  background: url("hero-3.4281f0ac.png") center / cover no-repeat;
  position: absolute;
  top: 35px;
  right: 10px;
}

.slider-text-wrapper {
  width: 210px;
  position: absolute;
  top: 25%;
  left: 22%;
}

.slider-text {
  font-size: 14px;
  font-weight: 400;
  line-height: 19.6px;
}

.slider-text-wrapper .icon-s {
  width: 26px;
  height: 26px;
  background-color: var(--White);
  border-radius: 50%;
  margin-top: 15px;
  padding: 7px;
}

.slider-text-wrapper .icon-s:hover {
  cursor: pointer;
  fill: var(--White);
  background-color: var(--Dark-Red-Pink);
}

@media (max-width: 779px) {
  .hero {
    overflow-x: hidden;
  }

  .container {
    width: 320px;
  }

  .hero-wrapper {
    padding: 150px 20px 0;
  }

  .hero-headings {
    z-index: 1;
  }

  .hero .heading-two {
    font-size: 48px;
  }

  .hero .heading-three {
    font-size: 26px;
  }

  .hero .button {
    margin-right: 5px;
    padding: 0 14px;
    font-size: 8px;
    font-weight: 400;
  }

  .hero-slider {
    padding-bottom: 147.5px;
  }

  .slider.one, .slider.three, .slider-text-wrapper, .hero-business-wrapper {
    display: none;
  }

  .slider.two {
    top: -185%;
    left: 10%;
  }

  .slider.two:after {
    width: 170px;
    height: 410px;
    background-size: cover;
    top: 3%;
    left: 35%;
  }

  .slider-bg {
    width: 345px;
    height: 345px;
  }
}

@media (min-width: 780px) and (max-width: 1199px) {
  .container {
    width: 780px;
  }

  .hero-wrapper {
    padding: 30px 32px 0;
  }

  .hero .heading-two {
    font-size: 34px;
  }

  .hero .heading-three {
    font-size: 22px;
  }

  .hero .button {
    margin-right: 5px;
    padding: 0 14px;
    font-size: 8px;
    font-weight: 400;
  }

  .hero-business .heading-two {
    font-size: 28px;
  }

  .hero-business p {
    font-size: 8px;
    line-height: normal;
  }

  .hero-business:before {
    width: 16px;
    height: 16px;
    top: -6px;
    right: -7px;
  }

  .hero-slider {
    padding-bottom: 17px;
  }

  .slider.one {
    width: 154px;
    height: 154px;
    background-size: cover;
    margin-top: 10px;
    margin-left: 32px;
  }

  .slider.two {
    top: -118.5%;
    right: 22.5%;
  }

  .slider-bg {
    width: 335px;
    height: 335px;
  }

  .slider.two:after {
    width: 145px;
    height: 335px;
    background-size: cover;
